<template>
  <el-container class="page-body">
    <!-- 头部 -->
    <el-header class="page-header">
      <div class="user-container">
        <!-- <el-dropdown> -->
        <!-- <i class="el-icon-setting" style="margin-right: 15px"></i> -->
        <!-- <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>查看</el-dropdown-item>
            <el-dropdown-item>新增</el-dropdown-item>
            <el-dropdown-item>删除</el-dropdown-item>
          </el-dropdown-menu> -->
        <!-- </el-dropdown> -->
        <!-- 用户 -->
        <a href="#/user" class="userlink">
          <i class="el-icon-user"></i>
        </a>
      </div>
    </el-header>
    <!-- 书本显示主体 -->
    <el-container class="main-container">
      <!-- logo -->
      <el-header class="logo-container">
        <a href="#/home" class="logo-text">NetLib</a>
      </el-header>
      <!-- 搜索框 -->
      <div style="margin-top: 15px">
        <el-input
          placeholder="请输入查找内容"
          v-model="search.searchText"
          class="input-with-select"
          >``
          <el-select v-model="select" slot="prepend" placeholder="请选择类别">
            <el-option label="名称" value="name"></el-option>
            <el-option label="作者" value="author"></el-option>
            <el-option label="出版社" value="address"></el-option>
          </el-select>
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="searchBookItem"
          ></el-button>
        </el-input>
      </div>

      <!--书本表格主体部分-->
      <div class="divider">
        <h1>最受欢迎的</h1>
        <el-divider></el-divider>
      </div>
      <!-- 书本信息表格 -->
      <el-main>
        <el-table :data="tableData">
          <el-table-column
            prop="type"
            align="center"
            label="类型"
            width="160px"
          >
          </el-table-column>
          <el-table-column
            prop="name"
            align="center"
            label="名称"
            width="260px"
          >
          </el-table-column>
          <el-table-column
            prop="author"
            align="center"
            label="作者"
            width="160px"
          >
          </el-table-column>
          <el-table-column
            prop="address"
            align="center"
            label="出版社"
            width="130px"
          >
          </el-table-column>
          <el-table-column fixed="right" align="center" label="操作">
            <template slot-scope="scope">
              <el-button
                @click="downloadBookItem(scope.$index, scope.row)"
                :icon="
                  scope.row.isDownload ? 'el-icon-check' : 'el-icon-download'
                "
                circle
              ></el-button>
              <el-button
                type="primary"
                icon="el-icon-edit"
                circle
                @click="editBookItem(scope.$index, scope.row)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--编辑界面-->
        <el-dialog
          title="编辑"
          :visible.sync="editDialogFormVisible"
          :close-on-click-modal="true"
          :append-to-body="true"
        >
          <!--editDialogForm表单提交的数据-->
          <el-form
            :model="editDialogForm"
            label-width="80px"
            ref="editDialogForm"
          >
            <el-form-item label="类型" prop="type">
              <el-input
                v-model="editDialogForm.type"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item label="姓名" prop="name">
              <el-input
                v-model="editDialogForm.name"
                auto-complete="off"
              ></el-input>
            </el-form-item>
            <el-form-item label="作者" prop="author">
              <el-input v-model="editDialogForm.author"></el-input>
            </el-form-item>
            <el-form-item label="出版社" prop="address">
              <el-input v-model="editDialogForm.address"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="editDialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="editDialogFormSubmit"
              >确 定</el-button
            >
          </div>
        </el-dialog>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    let initBookTable = [];
    for (var i = 0; i < 5; i++) {
      initBookTable.push({
        id: 0,
        type: "CS",
        name: "深入理解计算机系统",
        author: "xxx",
        address: "xxx",
        isDownload: false,
        fileURL: "",
      });
    }
    return {
      editDialogFormVisible: false,
      tableData: initBookTable,
      search: {
        type: "",
        searchText: "",
      },
      select: "name",
      //编辑界面数据
      editDialogForm: {},
    };
  },
  methods: {
    // 寻找书本
    searchBookItem() {
      let sbi = async () => {
        const { data: res } = await this.$http({
          method: "post",
          url: "/books",
          data: {
            type: this.search.type,
            text: this.search.searchText,
          },
        });
        if (res.flag === true) {
          this.tableData = res;
          this.$message.success("搜索成功");
        } else {
          this.$message.error("搜索失败");
        }
      };
      sbi();
    },
    // 下载书本
    downloadBookItem(index, row) {
      let dbi = async () => {
        const { data: res } = await this.$http.get("/books/download");
        row.fileURL = res.data;
        // console.log(row.fileURL)
        if (res.flag === true) {
          row.isDownload = true;
          window.open(row.fileURL, "_blank");
        } else {
          this.$message.error("下载失败");
        }
      };
      dbi();
    },
    // 编辑书本信息
    editBookItem(index, row) {
      // 获取表单信息
      this.editDialogFormVisible = true;
      this.editDialogForm = Object.assign({}, row);
    },
    // 对话表单提交
    editDialogFormSubmit() {
      var idx = this.editDialogForm.id;
      this.tableData.splice(idx, 1, this.editDialogForm);
      console.log(this.tableData);
      this.editDialogFormVisible = false;
      this.$message.success("修改成功");
    },
  },
};
</script>

<style lang="less" scoped>
// 主页容器
.page-body {
  height: 100%;
  width: 100%;
}

// 头部设置
.page-header {
  text-align: right;
  font-size: 20px;
  // background-color: yellow;
}

//用户容器样式
.user-container {
  margin: 10px;
  a {
    text-decoration: none;
    color: #333;
  }
}

// 内容容器
.main-container {
  width: 900px;
  line-height: 30px;
  margin: 0px auto;
}

// logo样式
.logo-container {
  text-align: center;
  .logo-text {
    font-size: 50px;
    text-decoration: none;
    font-family: "Courier New", Courier, monospace;
    color: #333;
  }
}

//搜索框样式
.header {
  // background-color: red;
  color: #333;
  line-height: 60px;
}

// 搜索框选择列表样式
.el-select {
  width: 130px;
}

// 设置分割线
.divider {
  margin-top: 25px;
  color: #49afd0;
  font-size: 16pt;
  // 分割线容器
  .el-divider {
    background-color: #49afd0;
  }
}
</style>

