<template>
  <el-container class="page-container">
    <!-- 网页头部 -->
    <el-header>
      <el-page-header class="page-header" @back="goBack" content="个人主页">
      </el-page-header>
    </el-header>
    <!-- 网页主体 -->
    <el-container>
      <!-- 侧边栏 -->
      <el-aside class="user-tabs-container">
        <!-- 用户头像 -->
        <div class="user-avatar-container">
          <el-avatar
            :size="200"
            src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"
          ></el-avatar>
        </div>
        <!-- 用户信息 -->
        <div class="userItem">
          <h1>个人信息</h1>
        </div>
      </el-aside>
      <!-- 用户行为主体 -->
      <el-main class="page-main-contain">
        <el-tabs tab-position="top">
          <!-- 用户收藏 -->
          <el-tab-pane label="我的收藏" >
            <el-table class="tableClass" :data="tableData">
              <el-table-column
                prop="type"
                align="center"
                label="类型"
                width="130px"
              >
              </el-table-column>
              <el-table-column
                prop="name"
                align="center"
                label="名称"
                width="200px"
              >
              </el-table-column>
              <el-table-column
                prop="author"
                align="center"
                label="作者"
                width="130px"
              >
              </el-table-column>
              <el-table-column
                prop="address"
                align="center"
                label="出版社"
                width="100px"
              >
              </el-table-column>
              <el-table-column  align="center" label="操作">
                <template slot-scope="scope">
                  <el-button
                    @click="takesBookItem(scope.row)"
                    :icon="scope.row.istakes"
                    circle
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
            <!-- 用户上传书籍 -->
          </el-tab-pane>
          <!-- 用户上传 -->
          <el-tab-pane label="我的上传">
            <el-button icon="el-icon-upload2"> </el-button>
          </el-tab-pane>
        </el-tabs>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    let initLikeTable = [];
    for (let i = 0; i <= 5; i++) {
      initLikeTable.push({
        type: "CS",
        name: "深入理解计算机系统",
        author: "xxx",
        address: "xxx",
        istakes: "el-icon-star-on",
      });
    }
    return {
      tableData: initLikeTable, //Array(5).fill(item),
    };
  },
  methods: {
    goBack() {
      this.$router.push("/home");
    },
    takesBookItem(row) {
      console.log(row);
      if (row.istakes === "el-icon-star-on") {
        row.istakes = "el-icon-star-off";
        // 修改数据库中like表
      } else {
        row.istakes = "el-icon-star-on";
        // 修改数据库中like表
      }
    },
  },
};
</script>

